.box {
  position: relative;
  background: #fff;
  border-radius: 4px;
  margin: 10px 15px;
  margin-bottom: 84px;
}

.shadow {
  box-shadow: 0 3px 24px 0 rgba(0, 0, 0, 0.12);
}

.NewButton {
  width: 95%;
  background: transparent;
  position: fixed;
  bottom: 84px;
  z-index: 99;
  height: 68px;
  display: flex;
  left: 0;
  justify-content: flex-end;
}
