body {
  margin: 0;
}

.app {
  display: flex;
}

.sidenav {
  width: 400px;
  position: fixed;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
}
.banner {
  width: 393px;
  height: 216px;
  padding: 8px 0 8px 8px;
  background-image: url(https://lh5.googleusercontent.com/p/AF1QipOwcGt4uKuY1EnUM6OKEIrunrPjWRCiHjQehebr=w448-h240-k-no);
}

.user-profile-image {
  width: 78px;
  margin: 131px 159px 0px 155px;
}

.people-heding-text {
  font-size: 16px;
  font-weight: 500;
  line-height: 18px;
  font-family: Roboto, sans-serif;
  text-align: left;
  padding: 12px;
  text-align: center;
  margin-top: 30px;
}
.people-icons {
  display: flex;
  justify-content: center;
}

.people-div {
  margin-bottom: 20px;
}

.donut-container-div {
  text-align: center;
  margin: 12px;
  font-family: Roboto, Arial, sans-serif;
}
.app-header-text {
  font-family: "Google Sans", Roboto, "Noto Sans Devanagari UI",
    "Noto Sans Bengali UI", "Noto Sans Telugu UI", "Noto Sans Tamil UI",
    "Noto Sans Gujarati UI", "Noto Sans Kannada UI", "Noto Sans Malayalam UI",
    "Noto Sans Gurmukhi UI", Arial, sans-serif;
  font-size: 1.19rem;
  font-weight: 400;
  line-height: 1.75rem;
  border-bottom: 1px solid lightgray;
}

.full-width {
  width: 100%;
  margin-left: 402px;
}

.app-header-div {
  padding: 16px 32px 16px 32px;
}
